<script>
import img from '../assets/fondoazul.png';
import profile from '../assets/user.png';
import ProgressBar from '../components/ProgressBar';

export default {
    name: 'DeptoBase',
    props: {
        goal:{
            required: true,
        },
        progress: {
            required: true,
        },
        name: {
            required: true,
        },
        depto: {
            required: true,
        },
        days: {
            required: true,
        },
        m3: {
