        },
        bonus_p: {
            required: true,
        }
        ,
        bonus_tot: {
            required: true,
        },
        asis: {
            required: true,
        },
        extra: {
            required: true,
        }
    },
    components: {ProgressBar},
    data() {
        return {
            background: img,
            user: profile
        }
    },
    filters:{
        split:  (value) => {
            if (!value) return '';
            value = value.toString()
            return value.split(' ').slice(0, -1).join(' ')
        },
        rand: (value) => {
            if (!value) return '';
            if (typeof value === 'number') {
                return value.toFixed(2);
            }
            return value;
        }
    }
}
</script>